.popup__close {
  width: 35px;
  height: 35px;
  position: absolute;
  top: 23px;
  right: 24px;
  cursor: pointer;
  z-index: 1;
  margin-left: auto; }
  @media (max-width: 767px) {
    .popup__close {
      top: 15px;
      right: 16px; } }

.popup__close:before {
  content: '';
  background: url(../img/cross.svg) center no-repeat;
  background-size: auto;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 2;
  transition: all 0.3s cubic-bezier(0.77, 0, 0.2, 0.85); }

.popup__close:after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 100%;
  background: #3379b733;
  z-index: 1;
  transition: all 0.3s cubic-bezier(0.77, 0, 0.2, 0.85);
  transform: scale(0.01); }

.popup__close:hover:after {
  transform: scale(1); }

.popup__close:hover:before {
  transform: scale(0.7) rotate(180deg); }

